






































import { Component, Vue } from 'vue-property-decorator';

@Component
export default class Login extends Vue {
  private isVisablePwd: boolean = false;

  public data() {
    return {
      form: {
        username: '',
        password: '',
      },
    };
  }

  private toggleEye() {
    this.isVisablePwd = !this.isVisablePwd;
  }
}
